.menu {
  background-color: #2C2C2C;
  overflow: hidden;

  &__top {
    margin-bottom: 42px;
  }

  &__content {
    box-sizing: border-box;
    padding: 21px 42px;
    height: 100vh;

    @include onTablet {
      padding: 21px 42px;

    }

    @include onPhone {
      padding: 21px 21px;
    }
  }


  &__list {
    list-style: none;
    margin: 0 0 48px;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    text-decoration: none;
    font-family: 'Poppins';
    font-weight: 700;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #FFFFFF;
    transition: all 0.3s;

    &:hover {
      border-color: #2060F6;
      transform: scale(1.2);
    }
  }

  &__call {
    display: block;
    text-decoration: none;
    width: max-content;

    &--phone {
      font-weight: 600;
      font-size: 16px;
      line-height: 27px;
      text-transform: uppercase;
      color: #1d1d1d;
      margin-bottom: 16px;
    }

    &--action {
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      text-transform: uppercase;
      color: #1d1d1d;
      letter-spacing: 2px;
      padding-bottom: 8px;
      border-bottom: 1px solid #1d1d1d;
    }
  }

}

@include onDesktop {
  .menu {
    display: none;
  }
}


.container--top--menu {
  @include onTablet {
    display: flex;
    gap: 36px;
    align-items: center;
  }
}

.container--top--menu {
  @include onPhone {
    display: flex;
    gap: 36px;
    align-items: center;
  }
}


